import CryptoJS from "crypto-js"
 var keyStr = 'bbbbbbbbbbbbbbbb';var iv = 'ccccccccccccccc';
var key = CryptoJS.enc.Utf8.parse(keyStr);
var iv = CryptoJS.enc.Utf8.parse(keyStr);    

export default{
     aesEncrypt(message){
    var  encrypted = CryptoJS.AES.encrypt(message, key, {   
        iv:iv,
        mode: CryptoJS.mode.CBC,
        padding:CryptoJS.pad.Pkcs7 
    });
   return encrypted.toString();  //base64

},
//encryptedDataciphertext.toString()Java
//var encryptedStr = encryptedData.ciphertext.toString(); 
//128Base64
//var encryptedHexStr = CryptoJS.enc.Hex.parse(encryptedStr);
//var encryptedBase64Str = CryptoJS.enc.Base64.stringify(encryptedHexStr);
//CryptoJS.enc.Hex.parseCryptoJS.enc.Base64.stringifyBase64CryptoJS.AES.decrypt
 aesDecrypt(ciphertext){
    var decrypted = CryptoJS.AES.decrypt(ciphertext,key,{ 
         iv:iv,
        mode: CryptoJS.mode.CBC,
        padding:CryptoJS.pad.Pkcs7 
    });
    return decrypted.toString(CryptoJS.enc.Utf8);//Utf8
}

}


